a.logo {
  font-size: 30px;
  color: #000;
  font-weight: 800;
}

ul {
  margin: 0;
  padding: 0;
}


.mainmenu ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.mainmenu li {
  display: inline-block;
}

.mainmenu li a {
  display: block;
  color:  #000;
  text-transform: uppercase;
  font-size: 18px;
  padding: 9px;
  text-decoration: none;
  position: relative;
}

.mainmenu li a:hover:after {
  position: absolute;
  left: 50%;
  bottom: 6px;
  height: 2px;
  background: #000;
  width: 30px;
  content: "";
  margin-left: -15px;
}

.d-block {
  display: block!important;
  margin-bottom: 38px;
  overflow: hidden;
}

.footer_menu ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.footer_menu li {
  display: inline-block;
}

.footer_menu li a {
  display: block;
  color:  #000;
  text-transform: uppercase;
  font-size: 12px;
  padding: 9px;
  text-decoration: none;
  position: relative;
}

.footer_menu li a:hover:after {
  position: absolute;
  left: 50%;
  bottom: 6px;
  height: 2px;
  background: #000;
  width: 30px;
  content: "";
  margin-left: -15px;
}




input [type=submit] {
  background: #000;
}

input[type="submit"] {
  background: #000;
  color: #fff;
  border: none;
  padding: 6px 32px;
}

input[type="text"] {
  padding: 4px 22px;
}

header.header {
  padding: 30px 0px;
}

img {
  width: 100%;
}

.content-block {
  padding: 75px;
}


.photo-wrapper {
  height: 190px;
  margin-bottom: 113px;
}

.photo-wrapper img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  margin-bottom: 20px;
}

.photo-wrapper h5 {
  color: #000;
  font-size: 18px;
}
 a:hover{
  text-decoration: none;
}

p.cat-name {
  color: #000;
  font-size: 14px;
  margin-bottom: 20px;
}

.load-more-btn {
  padding: 10px;
  float: left;
}
